$navbar-height: pxTorem(200px);
$subNavbar-height:pxTorem(78px);
.m-subnav {
    background: #f8f6ed;
    padding: 0 13%;
    font-size: 80%;
    height: $subNavbar-height
}

.m-nav {
    width: 100%;
    height: $navbar-height;
    background-color: $white;
    color: $white;
    padding-left: pxTorem(78px);
}

.m-navbar {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    // justify-content: space-between;
    background-color: $white;
    height: $navbar-height;
    @include mobile_576 {
        height: auto;
        background-color: transparent;
    }
    // Because flex properties aren't inherited, we need to redeclare these first
    // few properties so that content nested within behave properly.
    >.container,
    >.container-fluid {
        // display: flex;
        // flex-wrap: wrap;
        // align-items: center;
        // justify-content: space-between;
    }
    &__logo {
        @include mobile_576 {
            display: inline-flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            padding: 11px 0;
        }
    }
    &__list {
        display: flex;
        &.is-active {
            transform: translateY(0);
        }
        // @include media-breakpoint-down(xs) {
        //     display: block;
        //     position: absolute;
        //     transform: translateY(-100%);
        //     top: 0;
        //     left: 0;
        //     z-index: 1;
        //     transition: 0.3s cubic-bezier(1, 0.5, 0, 1);
        //     background-color: black;
        //     width: 100%;
        // }
    }
    &__sublink {
        display: flex;
        color: black;
        font-size: pxTorem(14px);
        &.is-active {
            transform: translateY(0);
        }
    }
    &__link {
        @include content-center;
        transition: 0.5s;
        color: black;
        padding: 0 pxTorem(16px);
        height: $navbar-height;
        >i {
            padding-right: pxTorem(4px);
        }
        &:hover {
            background-color: rgba(white, 0.3);
            color: white;
        }
    }
    &__social {
        display: flex;
        align-items: center;
        >a {
            padding-right: pxTorem(10px);
        }
    }
    // 按鈕
    &__button {
        position: relative;
        transition: 0.2s ease-in-out;
        z-index: 2;
        width: 50px;
        height: 50px;
        cursor: pointer;
        @include media-breakpoint-up(sm) {
            display: none;
        }
        $toggle-w: 22px;
        $toggle-h: 4px;
        $toggle-h-half: $toggle-h / 2;
        &-open {
            position: absolute;
            left: 50%;
            top: 50%;
            transition: 0;
            transform: translate(-50%, -50%);
            border-radius: $toggle-h-half;
            background-color: white;
            width: $toggle-w;
            height: $toggle-h;
            &::before,
            &::after {
                content: '';
                position: absolute;
                transition: 0.2s ease-in-out;
                border-radius: $toggle-h-half;
                background-color: white;
                width: 100%;
                height: 100%;
            }
            &::before {
                bottom: -200%;
            }
            &::after {
                bottom: 200%;
            }
        }
        &-close {
            position: absolute;
            left: 50%;
            top: 50%;
            transition: 0;
            transform: translate(-50%, -50%);
            visibility: hidden;
            opacity: 0;
            border-radius: $toggle-h-half;
            background-color: transparent;
            width: $toggle-w;
            height: $toggle-h;
            &::before,
            &::after {
                content: '';
                position: absolute;
                transition: 0.2s ease-in-out;
                border-radius: $toggle-h-half;
                background-color: white;
                width: 100%;
                height: 100%;
            }
            &::before {
                transform: rotate(45deg);
            }
            &::after {
                transform: rotate(-45deg);
            }
        }
        &:hover &-open {
            background-color: transparent;
            &::before,
            &::after {
                bottom: -200%;
            }
            &::before {
                right: calc(50% - #{$toggle-h-half});
                transform: rotate(45deg);
                transform-origin: calc(100% - #{$toggle-h-half}) center;
            }
            &::after {
                left: calc(50% - #{$toggle-h-half});
                transform: rotate(-45deg);
                transform-origin: calc(0% + #{$toggle-h-half}) center;
            }
        }
        &:hover &-close {
            background-color: transparent;
            &::before,
            &::after {
                bottom: 200%;
            }
            &::before {
                right: calc(50% - #{$toggle-h-half});
                transform: rotate(-45deg);
                transform-origin: calc(100% - #{$toggle-h-half}) center;
            }
            &::after {
                left: calc(50% - #{$toggle-h-half});
                transform: rotate(45deg);
                transform-origin: calc(0% + #{$toggle-h-half}) center;
            }
        }
        &.is-active &-open {
            visibility: hidden;
            opacity: 0;
        }
        &.is-active &-close {
            visibility: visible;
            opacity: 1;
        }
    }
    // 按鈕 end
    // 位置樣式
    &_abs_top {
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        z-index: 999;
    }
    &.is-fixtop {
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
        z-index: 999;
    }
}

ul.m-navbar__sub {
    display: flex;
    justify-content: flex-end;
    color: black;
    font-size: 0.875rem;
}