    &__e-img {
        @include size(26px, 32px);
        margin-right: pxTorem(15px);
    }
    &__link {
        display: flex;
        align-items: center;
        transition: 0.5s;
        border-bottom: 1px dashed $primary;
        padding: pxTorem(8px) 0;
        font-size: pxTorem(16px);
        font-weight: bolder;
        color: #4D4F5C;
        &:active,
        &.is-active {
            // background-color: $primary;
            color: #3B86FF;
        }
        &:hover {
            background-color: rgba($primary, 0.8);
            color: white;
        }
        &.is-disabled {
            opacity: 0.6;
            pointer-events: none;
        }
    }
    &__form {
        display: flex;
    }
    &__categories {
        li {
            padding: 0;
            border: none;
            padding-top: pxTorem(8px);
