.ScreenReadersOnly {
    position: absolute !important;
    width: 1px;
    height: 1px;
    left: 0%;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
}

.ScreenReadersOnly:active,
.ScreenReadersOnly:focus {
    position: fixed !important;
    width: auto;
    height: auto;
    left: 0;
    top: 0;
    overflow: visible;
    clip: auto;
}

