}

.searchBox input.btn {
    border: none;
    width: 55px;
    height: 31px;
    background: url(../../images/icon/search_W.svg) no-repeat center/36%;
    text-indent: -9999px;
    vertical-align: middle;
    background-color: #47a938;
    border-radius: 5px;
}

.searchBox dl {
    margin-bottom: 15px;
    margin-right: 15px;
}

.searchBox dl>* {
