    flex-wrap: wrap;
}

.searchBox dl>dd.fieldset-group select {
    max-width: 100px;
}


/*██████↓↓↓↓ Nav選單 ↓↓↓↓██████*/

.Nav {
    width: 100%;
    position: relative;
    z-index: 1000;
    // margin-bottom: 62px;
}

.topNavWrap {
    display: flex;
    justify-content: flex-end;
    width: auto;
    height: pxTorem(2px);
}

.NavFixed {
    position: fixed;
}

.NavFixed .topNav {
    background: #37942A;
    box-shadow: 2px 2px 7px rgba(0, 0, 0, 0.5);
}

.mainNavWrap {
    position: absolute;
    width: 100%;
    left: 0;
}

@-webkit-keyframes fadeInDown {
    from {
        opacity: 0;
        -webkit-transform: translate3d(0, -100%, 0);
        transform: translate3d(0, -100%, 0);
    }
    to {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }
}

@keyframes fadeInDown {
    from {
        opacity: 0;
        -webkit-transform: translate3d(0, -100%, 0);
        transform: translate3d(0, -100%, 0);
    }
    to {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }
}

.fadeInDown {
    -webkit-animation: 0.3s ease fadeInDown;
    animation: 0.3s ease fadeInDown;
    position: fixed;
    top: 0;
}

.mainNavWrap.fadeInDown .topNav {
    // background: $primary;
    // padding: 0;
    box-shadow: 2px 2px 7px rgba(0, 0, 0, 0.5);
}


