/*----純文字的----*/

.topNavWrap {
    .subNavTxt {
        display: flex;
        @include mobile_768 {
            display: none;
        }
    }
}

.subNavTxt li {
    margin: 0;
    position: relative;
    letter-spacing: 0.01em;
    display: flex;
    font-size: 12px;
    &:first-child {
        &:before {
            display: none;
        }
    }
    &:before {
        display: inline-block;
        padding: 0 .4rem;
        color: #000;
        content: "/";
    }
}

.subNavTxt li a {
    color: #2e2e2e;
    height: 25px;
    line-height: 25px;
}

.subNavTxt_right {
    display: flex;
    width: 100%;
    justify-content: flex-end;
    @include mobile_576 {
        align-items: center;
        width: 2%
    }
    @media screen and (max-width: 480px) {
        width: 31%;
        justify-content: center;
    }
    .login_btn {
        display: flex;
        align-items: flex-end;
        margin-bottom: 7px;
    }
    .search_btn {
        margin-right: pxTorem(30px);
        margin-bottom: pxTorem(17px);
        @include media-breakpoint-down(sm) {
            margin-bottom: pxTorem(8px);
        }
        @media screen and (max-width: 480px) {
            margin-right: pxTorem(13px);
        }
        a {
            border: 1px solid #f0f0f0;
            padding: 0;
            border-radius: 50%;
            background-color: #C9E7FE;
            transition: 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
            width: 35px;
            height: 35px;
            font-size: 20px;
            padding: 7px;
        }
    }
    &>li {
        // height: 75px;
        line-height: 54px;
        color: #707070;
        font-size: pxTorem(14px);
        margin-right: 13%;
        display: flex;
        align-items: flex-end;
    }
    &>li a {
        @include bg-cover;
        display: flex;
        align-items: center;
    }
    .menu_box {
        border: 1px solid #f0f0f0;
        padding: 0;
        border-radius: 50%;
        background-color: $main_color;
        transition: .4s cubic-bezier(.215, .61, .355, 1);
        font-size: pxTorem(14px);
        width: 35px;
        height: 35px;
        background-size: cover;
        margin-right: 20px;
    }
    .dsc {
        @include media-breakpoint-down(sm) {
            display: none;
        }
    }
}

