/*██████↓↓↓↓ MAIN(側選單+內容頁) ↓↓↓↓██████*/

.main {
    min-height: 800px;
    width: 100%;
    position: relative;
    z-index: 100;
    background: #F8F6ED;
    padding: 0 13%;
}

.main:after {
    content: ".";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
}


/*====== 側邊選單 ======*/

.main aside .acc,
.main .aside .acc {
    position: absolute;
}

.main aside,
.main .aside {
    width: 18%;
    float: left;
    margin-top: -92px;
}

.main aside h2,
.main .aside h2,
.main aside .fakeh2,
.main aside .aside-title,
.main .aside .fakeh2,
.main .aside .aside-title {
    display: table;
    width: calc(100% + 1px);
    height: 92px;
    background: #46a938;
    color: $white;
    padding: 0 15%;
    font-size: 125%;
}

.main aside h2 b,
.main .aside h2 b,
.main aside .fakeh2 b,
.main aside .aside-title b,
.main .aside .fakeh2 b,
.main .aside .aside-title b {
    display: table-cell;
    vertical-align: middle;
}


/*側邊選單-第一層*/

.leftBtns {
    background: $white;
    border: 1px solid #e5e0cc;
}

.leftBtns>li>a {
    background: url(../../images/icon/btn_right.svg) #FFF no-repeat right center/0px 0px;
    color: #46a938;
    display: block;
    padding: 18px 33px 18px 15%;
    border-bottom: 1px solid #eee;
}

.leftBtns>li>a:hover,
.leftBtns>li>a.focus {
    background: url(../../images/icon/btn_right.svg) #fbd116 no-repeat right center/38px 15px;
    color: #000;
}


/*側邊選單-第二層*/

.leftBtns ul {
    padding: 6px 5% 8px 15%;
    border-bottom: 1px solid #eee;
    display: none;
}

.leftBtns ul.focus {
    display: block;
}

.leftBtns ul li {
    margin: 12px 0;
}

.leftBtns ul li a {
    display: inline-block;
    font-size: 90%;
    padding-left: 16px;
    color: #8b8b8b;
    position: relative;
}

.leftBtns ul li a:before {
    content: "";
    width: 5px;
    height: 5px;
    background: #8b8b8b;
    border-radius: 50%;
    position: absolute;
    left: 1px;
    top: 9px;
}

.leftBtns ul li a.focus,
.leftBtns ul li a:hover {
    color: #46a938;
}

.leftBtns ul li a.focus:after,
.leftBtns ul li a:hover:after {
    content: "";
    width: calc(100% - 16px);
    height: 1px;
    background: #46a938;
    position: absolute;
    left: 15px;
    bottom: -3px;
}

.leftBtns ul li a.focus:before,
.leftBtns ul li a:hover:before {
    background: #46a938;
}

.leftBtns i {
    display: none;
}


