// breadcrumb
.main-content {
    max-width: 1440px;
    margin: 0 auto;
    clear: both;
    min-height: 560px;
}

.wrapper_color {
    background-color: #F0F0F7;
}

.e-breadcrumb {
    display: flex;
    flex-wrap: wrap;
    padding: pxTorem(30px) pxTorem(0px);
    // margin-bottom: pxTorem(36px);
    list-style: none;
    // margin-left: pxTorem(19px);
    // background-color: $menu_color;
    width: 100%;
    @include desktop_1024 {
        width: 100%;
        font-size: pxTorem(13px);
        margin-bottom: 0;
    }
    @include mobile_768 {
        margin-bottom: 0;
    }
    li {
        &:first-child {
            &:before {
                display: none;
            }
        }
        &:before {
            display: inline-block;
            padding: 0 .5rem;
            color: $font-color;
            content: "/";
        }
        &.active {
            color: $dark;
        }
    }
}

.e_news {
    h3 {
        color: $dark;
        font-size: pxTorem(20px);
        margin: 30px 0;
        i {
            color: #A5A4BF;
            padding-right: 10px;
            font-size: pxTorem(22px);
        }
    }
    // background-color: $white;
    display: flex;
    // align-items: center;
    flex-direction: column;
    margin-bottom: 20px;
    padding:0 pxTorem(16px);
    @media screen and (max-width: 1024px) {
        .switchpic {
            display: none!important;
        }
    }
    // 顏色樣式
    @each $name,
    $value in $theme-color {
        &_#{$name} {
            background-color: $value;
        }
    }
}

.items_news {
    border-radius: 1.25rem;
    border: 3px solid;
    background-color: $white;
    width: 100%;
    min-height: 40px;
    padding: 15px 20px;
    @each $name,
    $value in $theme-color {
        &_#{$name} {
            border-color: $value;
        }
    }
    .oneNews {
        h2 {
            display: block;
            font-size: 100%;
            color: #2f2f2e;
            margin-right: 10px;
            max-width: 100%;
            height: auto;
            font-size: pxTorem(12px);
            overflow: auto;
            text-overflow: initial;
            white-space: normal;
        }
        .newsDate {
            display: inline-block;
            background: #3D9AC6;
            padding: 4px;
            color: #FFF;
            font-size: 10px;
            border-radius: 10px;
            margin: 5px 0px 5px 0;
            @each $name,
            $value in $theme-color {
                &_#{$name} {
                    background-color: $value;
                }
            }
        }
        .PhotoDate {
            display: inline-block;
            background: #3D9AC6;
            padding: 4px 4px 4px 5px;
            font-size: 14px;
            margin-right: 10px;
            margin-bottom: 5px;
            border-radius: 10px;
            color: $white;
        }
    }
}

li.oneNews>a {
    display: block;
    padding: 3px 0;
}

.m-ponent {
    margin: 7.25rem 0;
    p {
        font-size: pxTorem(14px);
        line-height: 1.7em;
        color: $dark;
        font-weight: 400;
    }
    h2 {
        color: $dark;
        font-size: pxTorem(20px);
        margin: 10px 0 20px 0;
        text-align: center;
    }
    h3.articleTitle {}
    .article p {
        font-size: pxTorem(16px);
        line-height: 32px;
        color: $dark;
        margin-bottom: 10px;
    }
    @include mobile_768 {
        margin: 8rem 0;
    }
    @include mobile_576 {
        margin: 2rem 0
    }
}

