.e-btn {
    display: inline-block;
    border-radius: 15px;
    background-color: #eee;
    padding: pxTorem(8px) pxTorem(18px);
    color: white;
    font-size: pxTorem(16px);
    text-align: center;
    // height: pxTorem(32px);
    cursor: pointer;
    @media screen and (max-width: 480px) {
        padding: pxTorem(5px) pxTorem(10px);
    }
    &[type="submit"],
    &[type="reset"],
    &[type="button"] {
        padding: pxTorem(5px) pxTorem(30px);
    }
    &:hover,
    &:focus {
        box-shadow: pxTorem(1px) pxTorem(12px) pxTorem(14px) rgba(black, 0.4);
        background-color: $tab_color;
        color: white;
    }
    // 顏色樣式
    @each $name,
    $value in $theme-color {
        &_#{$name} {
            background-color: $value;
        }
    }
    // 寬樣式
    &_w_lg {
        width: pxTorem(200px);
    }
    &_list {
        display: inline-block;
        margin: 10px 4px 0;
        padding: 0 20px;
        line-height: 35px;
        color: #fff;
        cursor: pointer;
        // background-color: #dfdfdf;
        border-radius: 18px;
        &:hover {
            background-color: $primary;
        }
        @include mobile_768 {
            display: flex;
            border-radius: 0px
        }
    }
    &_disable {
        cursor: no-drop;
        pointer-events: none;
        background-color: $gray;
        &:disabled {
            cursor: no-drop;
        }
        &:hover,
        &:focus {
            box-shadow: none;
            background-color: $gray;
            color: white;
        }
    }
}

.otherClassBox {
    padding: 25px 0;
    text-align: center;
}