.e-map {
    @include full;
    min-height: pxTorem(300px);
}

.e-pagination {
    list-style: none;
    display: inline-block;
    padding: 0;
    margin-top: 10px;
    li {
        display: inline;
        text-align: center;
    }
    a {
        float: left;
        display: block;
        font-size: 14px;
        text-decoration: none;
        padding: 5px 12px;
        color: #fff;
        margin-left: -1px;
        border: 1px solid transparent;
        line-height: 1.5;
        &.active {
            cursor: default;
        }
        &:active {
            outline: none;
        }
    }
}

.modal-1 {
    li {
        &:first-child {
            a {
                border-radius: 6px 0 0 6px;
            }
        }
        &:last-child {
            a {
                border-radius: 0 6px 6px 0;
            }
        }
    }
    a {
        border-color: #ddd;
        color: #4285F4;
        background: #fff;
        &:hover {
            background: #eee;
        }
        &.active,
        &:active {
            border-color: #4285F4;
            background: #4285F4;
            color: #fff;
        }
    }
}

.e-sitemap {
    max-width: 980px;
    margin: 0 auto;
    .title {
        display: block;
        font-size: 20px;
        font-weight: bolder;
        color: #333;
        padding-bottom: 6px;
        border-bottom: 2px solid $primary;
        margin: 20px 0 10px 0;

    }
}