.e-form-square {
    display: flex;
    align-items: center;
    border: 1px solid #cccccc;
    background-color: white;
    width: 100%;
    height: pxTorem(40px);
    &__icon {
        color: rgba($primary, 0.5);
        padding: 0 pxTorem(9px);
        border-radius: pxTorem(3px);
    }
    &__input {
        background-color: transparent;
        flex: 1;
        &:focus {
            outline: none;
        }
    }
    &__vertify {
        width: 40%;
        display: flex;
        align-items: center;
        border: 1px solid #cccccc;
        background-color: white;
        height: pxTorem(40px);
    }
}

.e-formBox {
    // padding: 20px 0;
    font-size: pxTorem(14px);
    color: $dark;
    line-height: 1.8;
    letter-spacing: 0.6px;
    b {
        display: inline-block;
        margin-left: 5px;
        font-weight: 400;
        color: #9c9c9c;
    }
    label {
        display: inline-block;
        // margin-right: 7px;
        font-size: pxTorem(14px);
        color: $dark;
        font-weight: 400;
        line-height: 35px;
    }
    &_control {
        float: left;
        padding: 10px;
        width: 100%;
        box-sizing: border-box;
    }
    input[type="radio"] {
        appearance: none;
        padding: 0;
        width: 15px;
        height: 15px;
        border: 1px solid #999;
        border-radius: 50%;
        cursor: pointer;
        margin-bottom: -2px;
        margin-right: 5px;
        transition: all 0.4s ease;
    }
    label input[type="checkbox"] {
        border: 1px solid #e4e4e4;
        background: #e4e4e4;
    }
    input[type="radio"]:checked,
    input[type="checkbox"]:checked {
        background: #01afb8;
        border: 1px solid #01afb8;
    }
    .left {
        em {
            color: #ff3333;
            font-weight: 400;
            overflow: hidden;
        }
    }
    .right {
        position: relative;
    }
    .left {
        margin-bottom: 5px;
    }
    .form .right {
        font-weight: 400;
        overflow: hidden;
        line-height: 35px;
    }
    select {
        padding: 5px 10px;
        height: 35px;
        color: #000;
        border: 1px solid #e4e4e4;
        background: $white;
        border-radius: 5px;
        font-size: 1rem;
        letter-spacing: 0.5px;
        transition: all 0.4s ease;
    }
    textarea {
        color: #000;
        width: 100%;
        height: 100px;
        padding: 5px;
        border: 1px solid #e4e4e4;
        background: $white;
        border-radius: 0px;
        resize: none;
        font-size: 14px;
    }
    input[type="text"],
    input[type="password"] {
        outline: none;
        color: #000;
        padding: 5px 10px;
        width: 100%;
        height: 35px;
        line-height: 35px;
        letter-spacing: 1px;
        border: 1px solid #e4e4e4;
        background: $white;
        border-radius: 0;
        font-size: 1rem;
        transition: all 0.4s ease;
    }
    input[type="file"] {
        // position: absolute;
        // font-size: 50px;
        // opacity: 0;
        // right: 0;
        // top: 0;
        background: #6cb9b9;
        border: 1px solid #666;
        border-radius: 6px;
    }
    .file {
        padding: 5px 10px;
        font-size: 1rem;
        line-height: 30px;
        border-radius: 10px;
    }
}

[class^='g-'] {
    float: left;
    padding: 10px;
    width: 100%;
    box-sizing: border-box;
}

.e-formBox .right::before {
    content: '';
    width: 0;
    height: 2px;
    background: #000;
    position: absolute;
    top: 100%;
    left: 0;
    &:hover {
        width: 100%;
    }
}

.mem_des .e-formBox .left {
    text-align: left;
    @include mobile_768 {
        text-align: left;
    }
}

.e-formBox .right::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: 0%;
    height: 1px;
    background: #8b2538;
    transition: all .5s;
}

.othephone {
    padding: 0 5px;
    width: 43%;
    @include mobile_576 {
        padding: 0;
        width: 66%;
        white-space: nowrap;
    }
}

.e-formBox input[name="data_address"] {
    width: 300px;
}

@media (min-width: 1024px) {
    .g-tv-3 {
        width: 24.99%;
    }
    .g-tv-6 {
        width: 50%;
    }
    .g-tv-12 {
        width: 100%;
    }
}

@media (max-width: 767px) {
    .e-formBox .left {
        display: block;
        float: none;
        width: 100%;
        margin: 0 0 0px 0;
        text-align: left;
    }
    .e-formBox input[type="text"],
    .e-formBox input[type="password"] {
        width: 100%;
    }
}