//
// 特色
//
$line_bg:'data:image/svg+xml;base64,';
.p-feature {
    background-color: $bg_color;
    &:after {}
    &__title {
        font-size: pxTorem(35px);
        position: relative;
        padding: 0 pxTorem(16px);
        text-align: left;
        color: #707070;
        font-weight: $font-weight-bolder;
        top: pxTorem(-130px);
        padding-left: 5%;
        @media screen and (max-width: 1024px) {
            top: 0;
            font-size: 2.14286rem;
            padding: 0;
            margin: 20px 0;
        }
        &::before,
        &::after {
            content: '';
            position: absolute;
            top: -44px;
            bottom: 0;
            /* right: 0; */
            pointer-events: none;
            // top: 50%;
            // border-top: 1px solid $primary;
            // width: pxTorem(125px);
            @include media-breakpoint-down(sm) {
                display: none;
            }
        }
        &::before {
            @include bg-img(transparent, $line_bg);
            @include bg-cover;
            @include size(25px, 143px);
            left: -7px;
        }
    }
}

//
// 報名
//
.p-class {
    background-color: $white;
    background-image: url(https://upload.cc/i1/2020/08/29/vnacRp.png);
    background-repeat: no-repeat;
    background-position: left;
    background-size: 8%;
    @include media-breakpoint-down(sm) {
        background-image: none;
    }
    &__title {
        font-size: pxTorem(30px);
        position: relative;
        // padding: 0 pxTorem(16px);
        text-align: left;
        color: #707070;
        z-index: 1;
        font-weight: $font-weight-bolder;
        &::before,
        &::after {
            content: '';
            position: absolute;
            top: -7px;
            z-index: -1;
            left: 118px;
            bottom: 0;
            pointer-events: none;
        }
        &::after {
            // left: 100%;
            border-radius: 31px 25px 144px 29px;
            transform: rotate(139deg);
            width: 35px;
            height: 35px;
            background-color: $title_color;
            @include mobile_768 {}
        }
    }
    &__img {
        @include bg-cover;
        // height: pxTorem(460px);
        background-size: contain;
        background-position: center bottom;
        width: 100%;
        padding-bottom: 88%;
        @include media-breakpoint-down(sm) {
            padding-bottom: 67%;
        }
    }
    &__form {
        width: pxTorem(76px);
        height: pxTorem(75px);
        background-repeat: no-repeat;
        background-size: cover;
        background-image: url(data:image/svg+xml;base64,);
        @include desktop_1024 {
            background-image: url(./../img/undraw_preferences_popup_wbfw.svg);
            width: pxTorem(200px);
            height: pxTorem(135px);
            margin-bottom: 12px;
        }
        @include mobile_576 {
            display: none;
        }
    }
    &__button {
        @include mobile_576 {
            display: none;
        }
    }
    &__content {
        background-color: $white;
        border-radius: pxTorem(6px);
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: space-around;
        margin-top: 4rem;
        box-shadow: 0 8px 9px -5px rgba(0, 0, 0, .02), 0 15px 22px 2px rgba(19, 5, 5, .04), 0 6px 28px 5px rgba(0, 0, 0, .02);
        @include full;
        padding: 30px 15px;
        @include desktop_1024 {
            flex-direction: column;
            justify-content: center
        }
        @include mobile_576 {
            height: 208px;
        }
    }
    &__txtAD {
        border-bottom: 1px dashed #ccc;
        padding: 8px 0;
        text-overflow: ellipsis;
        max-width: calc(368px - 13px);
        overflow: hidden;
        color: #333;
        @include mobile_768 {
            max-width: 100%;
        }
        i {
            color: $btn-color;
            padding-right: pxTorem(5px);
        }
    }
}

//
// 試作服務
//
.p-class__block {}

.p-service {
    &__title {
        font-size: pxTorem(30px);
        position: relative;
        // padding: 0 pxTorem(16px);
        text-align: left;
        color: #707070;
        z-index: 1;
        display: inline-block;
        font-weight: $font-weight-bolder;
        &::before,
        &::after {
            content: '';
            position: absolute;
            // top: -7px;
            z-index: -1;
            left: 111px;
            bottom: 16px;
            pointer-events: none;
        }
        &::after {
            // left: 100%;
            border-radius: 31px 25px 144px 29px;
            transform: rotate(139deg);
            width: 25px;
            height: 25px;
            background-color: $title_color;
            @include mobile_768 {
                // width: 35px;
                // height: 35px;
            }
        }
    }
    &__content {
        background-color: $white;
        border-radius: pxTorem(6px);
        padding: 30px 15px;
        width: 100%;
        display: flex;
        box-shadow: 0 8px 9px -5px rgba(0, 0, 0, .02), 0 15px 22px 2px rgba(19, 5, 5, .04), 0 6px 28px 5px rgba(0, 0, 0, .02);
        @include mobile_768 {
            margin-bottom: 50px;
        }
        &:after {
            content: '';
            position: absolute;
            width: pxTorem(133px);
            height: pxTorem(76px);
            background-repeat: no-repeat;
            background-size: cover;
            // left: 27px;
            bottom: -66px;
            background-image: url(data:image/svg+xml;base64,);
            @media screen and (max-width: 1024px) {
                display: none;
            }
        }
    }
    &__img {
        position: relative;
        max-width: 160px;
        width: 40%;
    }
    &__box {
        flex: 1 1 0%;
        padding-left: 45px;
        ul li {
            margin: 15px 0;
            font-size: 1rem;
            color: #333;
            font-weight: 400;
        }
    }
}

.scroll {
    position: relative;
    li {
        height: 70px;
        font-size: 1.1rem;
    }
}

// p-serve
.p-tab__content {
    width: 100%;
    margin: 25px 0 50px 0;
    min-height: 327px;
    background-color: $white;
    border-radius: pxTorem(6px);
