        min-height: 300px;
        margin-bottom: 50px;
    }
    @include mobile_768 {
        // min-height: 321px;
        margin-bottom: 50px;
    }
}

//
// p-ad 廣告專區
.p-ad {
    position: relative;
    &__content {
        background-color: $white;
        border-radius: pxTorem(6px);
        padding: 30px 15px;
        width: 100%;
        min-height: 200px;
        display: flex;
        flex-direction: column;
        box-shadow: 0 8px 9px -5px rgba(0, 0, 0, 0.02), 0 15px 22px 2px rgba(19, 5, 5, 0.04), 0 6px 28px 5px rgba(0, 0, 0, 0.02);
        @include mobile_576 {
            padding: 3px 15px;
        }
    }
    &__img {
        margin: 10px auto;
    }
}

// 評論
//
.p-comments {
    background-color: $bg_color;
    background-image: url(https://upload.cc/i1/2020/08/29/h3KCcv.png);
    background-repeat: no-repeat;
    background-position: left bottom;
    background-size: 8%;
    position: relative;
    @include media-breakpoint-down(sm) {
        background-image: none;
    }
    &__comment {
        display: flex;
    }
    &__comment-text {
        display: flex;
        flex-direction: column;
        max-width: calc(100% - 146px);
        @include media-breakpoint-down(sm) {
            max-width: 100%;
            width: 100%
        }
        padding-left: 16px;
        p {
            font-size: pxTorem(15px);
            line-height: 2;
            color: $dark;
            font-weight: bolder;
            @include media-breakpoint-down(sm) {
                margin: 1.5rem 0
            }
        }
    }
    &__comment-title {
        height: pxTorem(67px);
        line-height: pxTorem(67px);
    }
    &__e-img {
        margin-right: pxTorem(12px);
        @include size(67px);
    }
    &::after {}
    .e-btn {
        color: $dark;
        font-weight: bolder;
        @include media-breakpoint-down(sm) {
            margin: 30px auto;
        }
        &:hover {
            color: $white;
        }
    }
}

.p-comments {
    .container::after {
        content: '';
        position: absolute;
        z-index: 1;
        // left: 130px;
        bottom: -20px;
        width: pxTorem(100px);
        height: pxTorem(59px);
        background-image: url(data:image/svg+xml;base64,);
        background-repeat: no-repeat;
        background-size: cover;
        @include media-breakpoint-down(sm) {
            display: none;
        }
    }
}

// 活動緊急
.p-activity {
    background-color: $white;
    background-image: url(https://upload.cc/i1/2020/08/29/PneU4W.png);
    background-repeat: no-repeat;
    background-position: 88% top;
    background-size: 8%;
}

// 頁數
.pages-change {
    li {
        float: left;
    }
    a {
        display: block;
        padding: pxTorem(15px);
        text-decoration: none;
        font-size: $font-b;
        background-color: $white;
        border: 1px solid #ECEEEF;
        color: $secondary;
        &:hover {
            color: $white;
            background-color: $secondary;
        }
    }
}

.result {
    display: none;
    margin-bottom: pxTorem(20px);
}

//右邊search 結果
.result {
    p {
        font-size: $font-l;
        color: $dark;
        margin-bottom: pxTorem(10px);
    }
    a {
        display: inline-block;
        margin-right: pxTorem(5px);
        padding: pxTorem(7px) pxTorem(20px);
        text-decoration: none;
        color: $primary;
        border: 1px solid $primary;
        border-radius: 100px;
        transition: all 0.3s linear;
        &:hover {
            background-color: $primary;
            color: $white;
        }
    }
}

@media (min-width: 768px) {
    .result {
        display: block;
        p {}
    }
}

