.plus {
    display: none;
    position: absolute;
    right: 0;
    bottom: 0;
    top: 6%;
    cursor: pointer;
    font-size: $font-b;
    color: $muted;
    padding: pxTorem(5px);
    @include mobile_576 {
        display: inline;
    }
    &-hide {
        display: none;
    }
}

.minus {
    display: none;
    position: absolute;
    cursor: pointer;
    right: 9%;
    &-pop {
        @include mobile_576 {
            display: inline;
        }
    }
}

