/*████↓↓--第一塊--↓↓████*/

.mainSection {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}


/*↓↓---左邊選單(等內容確定了裡面會調整)---↓↓*/

.leftBtn {
    position: absolute;
    top: 210px;
    height: 70vh;
    z-index: 99;
}

.leftBtn>li {
    margin-bottom: 20px;
    position: absolute;
}

.leftBtn>li.hotClick,
.leftBtn>li.disasterAlert {
    right: 0;
}

.leftBtn>li:nth-child(2) {
    top: 200px;
    width: 355px;
    transition: all .5s ease-in-out;
}

.leftBtn>li:nth-child(3) {
    top: 360px;
    width: 355px;
    z-index: -1;
    transition: all .5s ease-in-out;
}

.leftBtn>li:nth-child(2).goOut,
.leftBtn>li:nth-child(3).goOut {
    right: -350px;
    transition: all .5s ease-in-out;
}

.leftBtn>li>a {
    display: block;
    width: 43px;
    border-radius: 0 8px 8px 0;
    padding: 50px 10px 10px 10px;
    line-height: 1.15;
    position: absolute;
    right: -43px;
    top: 0;
}

@keyframes an1 {
    0% {
        transform: scale(1);
    }
    100% {
        transform: scale(1.2);
    }
}


/*████↑↑強迫第一個一開始就動↑↑████*/

li.slick-current .image.an1 {
    transform: scale(1.2);
    transition: transform 5s ease-out;
}


/*//////////////*/

li .image.an2 {
    transform: scale(1.2);
    transition: transform .20s ease-out;
    transition-delay: 2s
}

li.slick-current .image.an2 {
    transform: scale(1);
    transition: transform 15s ease-out;
}


