.newsSection {
    padding: 0 13%;
    position: relative;
}

.newsSection:before {
    content: "";
    display: block;
    position: absolute;
    border-radius: 50%;
    width: 300px;
    height: 300px;
    border: 3px dashed rgba(255, 255, 255, 0.7);
    right: -150px;
    bottom: 60px;
}

.newsWrap {
    position: relative;
    // margin-top: 42px;
    margin-bottom: 50px;
    width: 100%;
    @media screen and (max-width: 1024px) {
        padding: 0 22px;
    }
}

.newsWrap:after {
    content: ".";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
}

.newsBox>ul {
    width: 100%;
}

.newsBox>ul>li {
    width: 100%;
}

.newsBox>ul>li.Hot b.department {
