.snowbanner {
    width: 100%;
    height: 100vh;
    overflow: hidden;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999;
    pointer-events: none;
}

.snowbanner .snow-1 {
    background: url('../../images/snow.png');
    background-repeat: repeat;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    animation: animateone 10s infinite linear;
    background-size: 500px 500px;
}

@-webkit-keyframes animateone {
    0% {
        background-position: 0px 0px;
    }
    100% {
        background-position: 500px 500px;
    }
}

@keyframes animateone {
    0% {
        background-position: 0px 0px;
    }
    100% {
        background-position: 500px 500px;
    }
}

.snowbanner .snow-2 {
    display: none;
}

.snowbanner .snow-3 {
    background: url('../../images/snow.png');
    background-repeat: repeat;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    animation: animatethree 10s infinite linear;
    background-size: 300px;
}

@-webkit-keyframes animatethree {
    0% {
        background-position: 0px 0px;
    }
    100% {
        background-position: 300px 900px;
    }
}

@keyframes animatethree {
    0% {
        background-position: 0px 0px;
    }
    100% {
        background-position: 300px 900px;
    }
}

$img_line:'data:image/svg+xml;base64,';
.m-Marquee {
    position: relative;
    background-color: $white;
    padding: 50px 0px 67px 0px;
    @media screen and (max-width: 992px) {
        padding: 50px 22px 54px 22px;
    }
    @include mobile_768 {
        padding: 0px 22px 24px 22px;
    }
    &:before {
        content: '';
        position: absolute;
        top: -30%;
        right: 0;
        /* bottom: 0; */
        left: 14%;
        z-index: 2;
        @include bg-img(transparent,
        $img_line);
        @include bg-cover;
        width: pxTorem(51px);
        height: pxTorem(111px);
        @media screen and (max-width: 1024px) {
            display: none;
        }
    }
    &__bg {
        display: flex;
        position: relative;
        background-color: $search_color;
        border-radius: 15px;
        padding: 12px 15px;
        height: pxTorem(80px);
        width: 95%;
        max-width: 1440px;
        margin: 0 auto;
        overflow: hidden;
    }
    &__text {
        i {
            color: $dark;
        }
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: pxTorem(15px);
    }
    // &:after {
    //     content: '';
    //     position: absolute;
    //     top: 0;
    //     right: 0;
    //     bottom: 0;
    //     left: 0;
    //     @include bg-img($search_color,
    //     $img_line);
