    //     width: 20px;
    //     height: 30px;
    // }
}


/*焦點新聞*/

ul.tabs {
    margin: 0;
    padding: 0;
    list-style: none;
    height: 20px;
    display: flex;
    justify-content: space-between;
    /* 	border-bottom: 1px solid #333; */
    width: 100%;
    text-align: center;
    transition: all 0.2s;
    margin-left: 11px;
    @media screen and (max-width: 992px) {
        margin-bottom: 30px;
    }
    @include mobile_768 {
        display: none;
    }
}

ul.tabs li {
    float: left;
    margin: 0 20px;
    cursor: pointer;
    padding: 0px;
    height: 60px;
    width: 21%;
    font-size: pxTorem(16px);
    line-height: 60px;
    background-color: #fff;
    color: #707070;
    border-radius: 10px;
    overflow: hidden;
    position: relative;
    box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);
}

.text_wraper {
    display: flex;
    width: 35px;
    height: 35px;
    margin-right: 16px;
    border-radius: 50%;
    background-color: #D4E6FF;
}

ul.tabs i {
    margin-right: 25px;
    padding: 10px 7px;
    text-align: center;
    color: #4DCFE0;
}

ul.tabs .local {
    display: flex;
    display: flex;
    align-items: center;
    justify-content: center;
}

.tabs .tabs_text {
    margin-left: 2px;
}

.tab_last {
    border-right: 1px solid #333;
}

ul.tabs li:hover {
    background-color: #0B3B7C;
    color: #fff;
    box-shadow: 0 20px 30px 0 rgba(0, 0, 0, 0.1);
}

ul.tabs li.active {
    background-color: #0B3B7C;
    color: #fff;
    /* border-bottom: 1px solid #fff; */
    display: block;
}

ul.tabs li.active i {
    color: #3C94FF;
}

.tab_container {
    border-top: none;
    clear: both;
    float: left;
    width: 98%;
    background: $white;
    overflow: auto;
    box-shadow: 0px 3px 6px #00000029;
    margin-top: 98px;
    border-radius: 20px;
    margin-left: 24px;
    @media screen and (max-width: 992px) {
        margin-top: 40px;
    }
    @include mobile_576 {
        margin-left: 0;
    }
    &::after {
        content: '';
        position: absolute;
        bottom: -41px;
        right: 0;
        background-color: transparent;
        background-image: url(data:image/svg+xml;base64,);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
        width: 113px;
        height: 113px;
        // left: -32px;
    }
}

.tab_content {
    padding: 20px;
    display: none;
}

.tab_drawer_heading {
    display: none;
}

@media screen and (max-width:1024px) {
    .tabs {
        display: none;
    }
    .tab_drawer_heading {
        background-color: $secondary;
        color: #fff;
        border-top: 1px dashed #333;
        margin: 0;
        padding: 15px 20px;
        display: block;
        position: relative;
        cursor: pointer;
        user-select: none;
    }
    .d_active {
        background-color: $primary;
        color: #fff;
    }
}

.slideUp,
.slideDown {
    position: absolute;
    width: 0px;
    height: 0px;
    border: 11px solid #000;
    border-color: #fff transparent transparent transparent;
    right: 32px;
    bottom: 12px;
}

.slideUp {
    border-color: transparent transparent transparent #fff;
    bottom: 6px;
    right: 5px;
}


