// Font
// base
$browser-default-font-size: 14px !default; // rem 基礎單位 放在 html 中
$base-font-size: 1rem;
$base-letter-spacing: $base-font-size * 0.08;
$base-line-height: 1.5;
// font-size
$font-xxl: $base-font-size * 2.25;
$font-xl: $base-font-size * 1.5;
$font-l: $base-font-size * 1.25;
$font-m: $base-font-size * 1.125;
$font-b: $base-font-size;
$font-s: $base-font-size * 0.8;
$font-es: $base-font-size * 0.75;
// fletter-spacing
$lsp-xxl: $base-letter-spacing * 2.25;
$lsp-xl: $base-letter-spacing * 1.5;
$lsp-l: $base-letter-spacing * 1.25;
$lsp-m: $base-letter-spacing * 1.125;
$lsp-b: $base-letter-spacing;
$lsp-s: $base-letter-spacing * 0.8;
$lsp-es: $base-letter-spacing * 0.75;
// font-weight
$font-weight-thin: 100;
$font-weight-light: 300;
$font-weight-regular: 400;
$font-weight-medium: 500;
$font-weight-bold: 700;
$font-weight-black: 900;
// google fonts
$NotoSansTC: 'Noto Sans TC',
sans-serif;
$Roboto: 'Roboto',
sans-serif;