.m-member-form {
    border-top: 3px solid $primary;
    padding: pxTorem(14px);
    background-color: $white;
    &__title {
        font-weight: $font-weight-bold;
        text-align: center;
    }
    &__label {
        font-weight: $font-weight-regular;
    }
    &__buttons {
        display: flex;
        justify-content: space-between;
    }
}

