$hero-img_desktop: './../img/top_bg.svg';
$hero-img_mobile: './../img/top_bg.svg';
$hero-height: pxTorem(500px);
.m-hero {
    position: relative;
    overflow: hidden;
    // 模糊背景
    &::before,
    &::after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
    }
    &::before {
        z-index: -1;
        @include bg-img(black, $hero-img_desktop);
        @include bg-cover;
        // filter: blur(15px);
    }
    &::after {
        z-index: -21;
        background-color: black;
    }
    // 背景
    &__bg {
        @include bg-img(black, $hero-img_desktop);
        @include bg-cover;
        width: 100vw;
        height: $hero-height;
        @include mobile_768 {
            height: pxTorem(400px);
        }
        @include media-breakpoint-down(sm) {
            @include bg-img(black, $hero-img_mobile);
        }
    }
    // 文本內容
    &__content {
        @include container-center;
        background-color: rgba(black, 0.35);
        padding: pxTorem(20px);
        min-width: pxTorem(300px);
        color: white;
        text-align: center;
    }
    // 標題
    &__title {
        margin-bottom: pxTorem(16px);
    }
}

.m-slider {
    position: relative;
    padding-top: 2%;
    background: #fff;
    padding-bottom: 39px;
}

.blog-slider {
    width: 95%;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    max-width: 1440px;
    margin: auto;
    background: $white;
    box-shadow: 0 20px 30px 0 rgba(51, 51, 51, 0.1);
    padding: 0px;
    border-radius: 25px;
    height: 500px;
    transition: all .3s;
    @media screen and (max-width: 992px) {
        // max-width: 680px;
        // height: 400px;
        max-width: 88%;
        height: 74vh;
        margin: 20px auto;
    }
    @media screen and (max-width: 768px) {
        min-height: 500px;
        max-width: 100%;
        height: 30vh;
        // height: auto;
        // margin: 77px auto;
    }
    @media screen and (max-height: 500px) and (min-width: 992px) {
        height: 350px;
    }
    @include mobile_576 {
        min-height: 400px;
    }
    &__item {
        display: flex;
        align-items: center;
        @media screen and (max-width: 768px) {
            flex-direction: column;
        }
        &.swiper-slide-active {
            .blog-slider__img {
                background-size: cover;
                height: 100%;
                max-width: 100%;
                background-position: center center;
                @media screen and (max-width: 576px) {
                    height: 400px;
                }
                img {
                    opacity: 1;
                    transition-delay: .3s;
                }
            }
            .blog-slider__content {
                >* {
                    opacity: 1;
                    transform: none;
                    @for $i from 0 to 15 {
                        &:nth-child(#{$i + 1}) {
                            transition-delay: $i * 0.1 + 0.3s;
                        }
                    }
                }
            }
        }
    }
    &__img {
        // width: 40%;
        width: 100%;
        flex-shrink: 0;
        // height: 300px;
        border-radius: 20px;
        // transform: translateX(-80px);
        overflow: hidden;
        // &:after {
        //     // content: '';
        //     position: absolute;
        //     top: 0;
        //     left: 0;
        //     width: 100%;
        //     height: 100%;
        //     background-image: linear-gradient(147deg, #fe8a39 0%, #fd3838 74%);
        //     border-radius: 20px;
        //     opacity: 0.8;
        // }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            display: block;
            opacity: 0;
            border-radius: 20px;
            transition: all .3s;
        }
        @media screen and (max-width: 992px) {
            // width: 45%;
        }
        @media screen and (max-width: 768px) {
            // transform: translateY(-50%);
            width: 100%;
            padding-bottom: 55%;
        }
        @media screen and (max-width: 576px) {
            width: 100%;
            padding-bottom: 50%;
        }
        @media screen and (max-height: 500px) and (min-width: 992px) {
            height: 270px;
        }
    }
    &__content {
        // width: 60%;
        padding-right: 25px;
        @media screen and (max-width: 992px) {
            // width: 55%;
        }
        @media screen and (max-width: 768px) {
            margin-top: -80px;
            text-align: center;
            padding: 0 30px;
        }
        @media screen and (max-width: 576px) {
            padding: 0
        }
        >* {
            opacity: 0;
            transform: translateY(25px);
            transition: all .4s;
        }
    }
    &__code {
        color: #7b7992;
        margin-bottom: 15px;
        display: block;
        font-weight: 500;
    }
    &__title {
        font-size: 24px;
        font-weight: 700;
        color: #0d0925;
        margin-bottom: 20px;
    }
    &__text {
        color: #4e4a67;
        margin-bottom: 30px;
        line-height: 1.5em;
    }
    // &__button {
    //     display: inline-flex;
    //     background-image: linear-gradient(147deg, #fe8a39 0%, #fd3838 74%);
    //     padding: 15px 35px;
    //     border-radius: 50px;
    //     color: #fff;
    //     box-shadow: 0px 14px 80px rgba(252, 56, 56, 0.4);
    //     text-decoration: none;
    //     font-weight: 500;
    //     justify-content: center;
    //     text-align: center;
    //     letter-spacing: 1px;
    //     @media screen and (max-width: 576px) {
    //         width: 100%;
    //     }
    // }
    .swiper-container-horizontal>.swiper-pagination-bullets,
    .swiper-pagination-custom,
    .swiper-pagination-fraction {
        bottom: 10px;
        left: 0;
        width: 100%;
    }
    &__pagination {
        position: absolute;
        z-index: 21;
        right: 20px;
        width: 11px!important;
        text-align: center;
        left: auto!important;
        top: 50%;
        bottom: auto!important;
        transform: translateY(-50%);
        @media screen and (max-width: 768px) {
            transform: translateX(-50%);
            left: 50%!important;
            top: 95%;
            width: 100%!important;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        &.swiper-pagination-bullets .swiper-pagination-bullet {
            margin: 8px 0;
            @media screen and (max-width: 768px) {
                margin: 0 5px;
            }
        }
        .swiper-pagination-bullet {
            width: 11px;
            height: 11px;
            display: block;
            border-radius: 10px;
            background: $primary;
            opacity: 1;
            transition: all .3s;
            &-active {
                opacity: 1;
                background: $info;
                height: 30px;
                box-shadow: 0px 0px 20px rgba(252, 56, 56, 0.3);
                @media screen and (max-width: 768px) {
                    height: 11px;
                    width: 30px;
                }
            }
        }
    }
}