// Reboot
//
//
//
// Document
//
*,
*::before,
*::after {
    box-sizing: border-box;
}

html {
    transition: 0.5s all;
    font-size: $browser-default-font-size;
}

html,
body {
    height: 100%
}

//
// Body
//
body {
    background-color: $bg_color;
    color: $mainTab-color;
    font-family: 'Noto Sans TC', "Microsoft JhengHei", "微軟正黑體", Arial, Helvetica, Sans-Serif;
    // font-size: $base-font-size;
    // font-weight: $font-weight-light;
    line-height: $base-line-height;
    letter-spacing: $base-letter-spacing;
}

//
// Headings and paragraphs
//
h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: $font-weight-medium;
}

h1 {
    letter-spacing: $lsp-xxl;
    font-size: $font-xxl;
}

h2 {
    letter-spacing: $lsp-xl;
    font-size: $font-xl;
}

h3 {
    letter-spacing: $lsp-l;
    font-size: $font-l;
}

h4 {
    letter-spacing: $lsp-m;
    font-size: $font-m;
}

h5 {
    letter-spacing: $lsp-b;
    font-size: $font-b;
}

h6 {
    letter-spacing: $lsp-s;
    font-size: $font-s;
}

//
// Links
//
a {
    text-decoration: none;
    color: $mainTab-color;
    &:hover {
        color: $primary;
    }
}

//
// forms
//
input,
label,
select,
button,
textarea {
    font-family: $Roboto;
}

input:focus {
    outline: none;
}

::placeholder {
    color: $dark;
    font-size: $font-b;
    @include mobile_576 {
        color: $dark;
        font-style: italic;
        padding: 0;
    }
}

img {
    max-width: 100%;
    height: auto;
    vertical-align: middle;
    border-style: none;
}